[class^='icon-'],
[class*=' icon-'] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.ui_ico_senti {
    @include inlineBlock();
    position: relative;
    width: 16px;
    height: 16px;
    vertical-align: top;
    text-align: left;

    > span {
        display: block;
        text-indent: -9999px;
    }

    &:before {
        position: absolute;
        top: 0;
        left: 0;
        font-size: 16px;
        font-family: 'icomoon';
    }

    &.is-positive:before {
        content: '\e012';
        color: $senti_positive_color;
    }
    &.is-negative:before {
        content: '\e013';
        color: $senti_negative_color;
    }
    &.is-neutral:before {
        content: '\e014';
        color: $senti_neutral_color;
    }
}

